import React from 'react';
import { useSelector } from 'react-redux';
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
} from 'chart.js';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

const Portfolio = () => {
  const { stocks } = useSelector(state => state.portfolio);

  const portfolioValue = stocks.reduce((total, stock) => total + (stock.shares * stock.price), 0);
  const totalGain = stocks.reduce((total, stock) => {
    const gain = (stock.price - stock.purchasePrice) * stock.shares;
    return total + gain;
  }, 0);
  const gainPercentage = (totalGain / portfolioValue) * 100;

  const chartData = {
    labels: stocks.map(stock => stock.symbol),
    datasets: [
      {
        label: 'Portfolio Value',
        data: stocks.map(stock => stock.shares * stock.price),
        backgroundColor: 'rgba(75, 192, 192, 0.2)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1
      }
    ]
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: 'Portfolio Distribution'
      }
    }
  };

  return (
    <div className="portfolio">
      <h2>Your Portfolio</h2>
      <div className="portfolio-stats">
        <div className="stat-item">
          <span>Total Value:</span>
          <span>${portfolioValue.toLocaleString()}</span>
        </div>
        <div className="stat-item">
          <span>Total Gain/Loss:</span>
          <span className={totalGain >= 0 ? 'positive' : 'negative'}>
            ${Math.abs(totalGain).toLocaleString()} ({gainPercentage.toFixed(2)}%)
          </span>
        </div>
      </div>
      <div className="chart-container">
        <Line data={chartData} options={chartOptions} />
      </div>
      <div className="stock-list">
        {stocks.map(stock => (
          <div key={stock.symbol} className="stock-card">
            <h3>{stock.symbol}</h3>
            <p>Shares: {stock.shares}</p>
            <p>Current Price: ${stock.price.toFixed(2)}</p>
            <p>Purchase Price: ${stock.purchasePrice.toFixed(2)}</p>
            <div className="stock-change">
              <span className={totalGain >= 0 ? 'positive' : 'negative'}>
                ${((stock.price - stock.purchasePrice) * stock.shares).toFixed(2)}
              </span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Portfolio; 