export const buyStock = (symbol, shares, price) => ({
  type: 'BUY_STOCK',
  payload: {
    symbol,
    shares,
    price,
    purchasePrice: price
  }
});

export const sellStock = (symbol, shares, price) => ({
  type: 'SELL_STOCK',
  payload: {
    symbol,
    shares,
    price
  }
});

export const updateStockPrice = (symbol, price) => ({
  type: 'UPDATE_STOCK_PRICE',
  payload: {
    symbol,
    price
  }
}); 