const initialState = {
  stocks: [],
  loading: false,
  error: null
};

const marketReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'FETCH_STOCK_PRICES_REQUEST':
      return {
        ...state,
        loading: true,
        error: null
      };

    case 'FETCH_STOCK_PRICES_SUCCESS':
      return {
        ...state,
        loading: false,
        stocks: action.payload
      };

    case 'FETCH_STOCK_PRICES_FAILURE':
      return {
        ...state,
        loading: false,
        error: action.payload
      };

    case 'UPDATE_MARKET_PRICES':
      return {
        ...state,
        stocks: state.stocks.map(stock => ({
          ...stock,
          price: action.payload[stock.symbol] || stock.price
        }))
      };

    default:
      return state;
  }
};

export default marketReducer; 