import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { fetchNews } from '../actions/newsActions';

const News = () => {
  const dispatch = useDispatch();
  const { news, loading, error } = useSelector(state => state.news);
  const [filter, setFilter] = useState('all');

  useEffect(() => {
    dispatch(fetchNews());
  }, [dispatch]);

  const filteredNews = news.filter(item => {
    if (filter === 'all') return true;
    return item.category === filter;
  });

  if (loading) return <div>Loading news...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div className="news">
      <h2>Financial News</h2>
      <div className="news-filters">
        <button
          className={filter === 'all' ? 'active' : ''}
          onClick={() => setFilter('all')}
        >
          All
        </button>
        <button
          className={filter === 'market' ? 'active' : ''}
          onClick={() => setFilter('market')}
        >
          Market
        </button>
        <button
          className={filter === 'stocks' ? 'active' : ''}
          onClick={() => setFilter('stocks')}
        >
          Stocks
        </button>
        <button
          className={filter === 'economy' ? 'active' : ''}
          onClick={() => setFilter('economy')}
        >
          Economy
        </button>
      </div>
      <div className="news-list">
        {filteredNews.map(item => (
          <div key={item.id} className="news-card">
            <h3>{item.title}</h3>
            <p className="news-source">{item.source}</p>
            <p className="news-date">{new Date(item.date).toLocaleDateString()}</p>
            <p className="news-summary">{item.summary}</p>
            <a href={item.url} target="_blank" rel="noopener noreferrer" className="read-more">
              Read More
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default News; 