const initialState = {
  cash: 100000,
  stocks: [],
  netWorth: 100000
};

const portfolioReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'BUY_STOCK': {
      const { symbol, shares, price, purchasePrice } = action.payload;
      const existingStock = state.stocks.find(stock => stock.symbol === symbol);
      const totalCost = shares * price;

      if (state.cash < totalCost) return state;

      const newStocks = existingStock
        ? state.stocks.map(stock =>
            stock.symbol === symbol
              ? {
                  ...stock,
                  shares: stock.shares + shares,
                  price,
                  purchasePrice: (stock.purchasePrice * stock.shares + purchasePrice * shares) / (stock.shares + shares)
                }
              : stock
          )
        : [...state.stocks, { symbol, shares, price, purchasePrice }];

      return {
        ...state,
        cash: state.cash - totalCost,
        stocks: newStocks,
        netWorth: state.cash - totalCost + newStocks.reduce((total, stock) => total + (stock.shares * stock.price), 0)
      };
    }

    case 'SELL_STOCK': {
      const { symbol, shares, price } = action.payload;
      const existingStock = state.stocks.find(stock => stock.symbol === symbol);
      if (!existingStock || existingStock.shares < shares) return state;

      const totalValue = shares * price;
      const newStocks = existingStock.shares === shares
        ? state.stocks.filter(stock => stock.symbol !== symbol)
        : state.stocks.map(stock =>
            stock.symbol === symbol
              ? { ...stock, shares: stock.shares - shares }
              : stock
          );

      return {
        ...state,
        cash: state.cash + totalValue,
        stocks: newStocks,
        netWorth: state.cash + totalValue + newStocks.reduce((total, stock) => total + (stock.shares * stock.price), 0)
      };
    }

    case 'UPDATE_STOCK_PRICE': {
      const { symbol, price } = action.payload;
      const newStocks = state.stocks.map(stock =>
        stock.symbol === symbol ? { ...stock, price } : stock
      );

      return {
        ...state,
        stocks: newStocks,
        netWorth: state.cash + newStocks.reduce((total, stock) => total + (stock.shares * stock.price), 0)
      };
    }

    default:
      return state;
  }
};

export default portfolioReducer; 