import axios from 'axios';

export const fetchStockPrices = () => async (dispatch) => {
  dispatch({ type: 'FETCH_STOCK_PRICES_REQUEST' });
  try {
    const response = await axios.get('https://api.example.com/stocks');
    dispatch({
      type: 'FETCH_STOCK_PRICES_SUCCESS',
      payload: response.data
    });
  } catch (error) {
    dispatch({
      type: 'FETCH_STOCK_PRICES_FAILURE',
      payload: error.message
    });
  }
};

export const updateMarketPrices = (prices) => ({
  type: 'UPDATE_MARKET_PRICES',
  payload: prices
}); 