import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { buyStock } from '../actions/portfolioActions';
import { fetchStockPrices } from '../actions/marketActions';

const Market = () => {
  const dispatch = useDispatch();
  const { stocks, loading, error } = useSelector(state => state.market);
  const { cash } = useSelector(state => state.portfolio);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortBy, setSortBy] = useState('symbol');

  useEffect(() => {
    dispatch(fetchStockPrices());
  }, [dispatch]);

  const handleBuy = (stock) => {
    const shares = Math.floor(cash / stock.price);
    if (shares > 0) {
      dispatch(buyStock(stock.symbol, shares, stock.price));
    }
  };

  const filteredStocks = stocks
    .filter(stock => 
      stock.symbol.toLowerCase().includes(searchTerm.toLowerCase()) ||
      stock.name.toLowerCase().includes(searchTerm.toLowerCase())
    )
    .sort((a, b) => {
      if (sortBy === 'price') return b.price - a.price;
      if (sortBy === 'change') return b.change - a.change;
      return a.symbol.localeCompare(b.symbol);
    });

  if (loading) return <div>Loading market data...</div>;
  if (error) return <div>Error: {error}</div>;

  return (
    <div className="market">
      <h2>Market</h2>
      <div className="market-controls">
        <input
          type="text"
          placeholder="Search stocks..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="search-input"
        />
        <select value={sortBy} onChange={(e) => setSortBy(e.target.value)} className="sort-select">
          <option value="symbol">Sort by Symbol</option>
          <option value="price">Sort by Price</option>
          <option value="change">Sort by Change</option>
        </select>
      </div>
      <div className="stock-list">
        {filteredStocks.map(stock => (
          <div key={stock.symbol} className="stock-card">
            <h3>{stock.symbol}</h3>
            <p className="stock-name">{stock.name}</p>
            <p className="stock-price">${stock.price.toFixed(2)}</p>
            <div className="stock-change">
              <span className={stock.change >= 0 ? 'positive' : 'negative'}>
                {stock.change >= 0 ? '+' : ''}{stock.change.toFixed(2)}%
              </span>
            </div>
            <button
              onClick={() => handleBuy(stock)}
              className="buy-button"
              disabled={cash < stock.price}
            >
              Buy
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Market; 